import React, { useState, useEffect } from "react";

import { authFetch } from "auth";
import { Grid, Button, TextField, CardHeader } from "@material-ui/core";
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import Snackbar from "components/Snackbar/Snackbar.js";

import { makeStyles } from "@material-ui/core/styles";
import styles from "assets/jss/material-dashboard-react/components/cardHeaderStyle.js";

const useStyles = makeStyles(styles);

export default function Alimentation() {
  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");

  const [cyclesList, setCyclesList] = useState(undefined);
  const [cycle, setCycle] = useState(undefined);

  const [poidsAM, setPoidsAM] = useState(undefined);
  const [poidsPM, setPoidsPM] = useState(undefined);
  const [date, setDate] = useState(new Date().toISOString().substring(0, 10));

  const classes = useStyles();

  useEffect(() => {
    var url = "/api/alimentation/" + date;
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        console.log(response.cycles);
        setCyclesList(
          response.cycles.sort((a, b) => (a.Bassin > b.Bassin ? 1 : -1))
        );
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [date]);

  useEffect(() => {
    if (cycle && cycle.ali) {
      if (cycle.Poids_pm) {
        setPoidsAM(cycle.Poids - cycle.Poids_pm);
        setPoidsPM(cycle.Poids_pm);
      } else {
        setPoidsAM(cycle.Poids);
        setPoidsPM(0);
      }
    } else if (cycle) {
      setPoidsAM(cycle["poids aliment a donner"]);
      setPoidsPM(0);
    }
  }, [cycle]);

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  const handleConfirm = () => {
    let temp = cyclesList;
    temp[cycle.id] = {
      ...temp[cycle.id],
      ali: true,
      Poids: poidsAM + poidsPM,
      Poids_pm: poidsPM,
      maj: true,
    };
    setCyclesList(temp);
    var url = "/api/alimentation";
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        bassin: cycle.Bassin,
        poids: poidsAM + poidsPM,
        poids_pm: poidsPM,
        date: date,
      }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.status === 401) {
          return null;
        } else if (response.status === 500) {
          return null;
        }
        return response.json();
      })
      .then((response) => {
        response !== null
          ? handleSnackbar("success", response.message)
          : handleSnackbar("warning", "Bad request");
      });
  };

  const changeDate = (d) => {
    var result = new Date(date + "T12:00:00");
    result.setDate(result.getDate() + d);
    console.log(result, result.toISOString());
    setDate(result.toISOString().substring(0, 10));
    setCycle(undefined);
  };

  return (
    <div>
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <Grid container spacing={2}>
        <Grid style={{ padding: "0" }} item xs={12} container justify="center">
          <Button
            onClick={() => changeDate(-1)}
            variant="contained"
            color="primary"
          >
            {"<<"}
          </Button>
          <Button
            style={{ marginLeft: "5px" }}
            variant="contained"
            color="primary"
          >
            {date.split("-").reverse().join("-")}
          </Button>
          <Button
            style={{ marginLeft: "5px" }}
            onClick={() => changeDate(1)}
            variant="contained"
            color="primary"
          >
            {">>"}
          </Button>
        </Grid>
        <Grid
          container
          spacing={1}
          item
          xs={4}
          style={{ overflow: "scroll", height: "100vh" }}
        >
          {cyclesList &&
            cyclesList.map((c, key) => (
              <Grid item xs={12} key={key}>
                <Button
                  style={{
                    height: "80px",
                    color: "white",
                    backgroundColor: c.ali ? "#4caf50" : "#3f51b5",
                  }}
                  id={key}
                  variant="contained"
                  fullWidth
                  onClick={(e) => {
                    e.target.id === ""
                      ? setCycle({
                          ...cyclesList[e.target.offsetParent.id],
                          id: e.target.offsetParent.id,
                        })
                      : setCycle({
                          ...cyclesList[e.target.id],
                          id: e.target.id,
                        });
                  }}
                >
                  {c.Bassin}
                </Button>
              </Grid>
            ))}
        </Grid>
        <Grid item xs={8}>
          {cycle ? (
            <Card>
              <CardHeader
                className={classes.cardHeader}
                title={cycle.Bassin}
                titleTypographyProps={{ variant: "h2", align: "center" }}
                style={{
                  backgroundColor: "#009688",
                  padding: "20px",
                  color: "white",
                  fontSize: "2rem",
                }}
              ></CardHeader>
              <CardBody>
                <Grid container spacing={2} alignItems="center">
                  <Grid item xs={3}>
                    <Card style={{ marginBottom: "10px" }}>
                      <h4 style={{ textAlign: "center" }}>
                        Peso de alimento que se debe dar:{" "}
                        {cycle["poids aliment a donner"]}
                      </h4>
                      <p>Aliment: {cycle.Type_Aliment}</p>
                    </Card>
                  </Grid>
                  <Grid item xs={3} style={{ textAlign: "center" }}>
                    <TextField
                      variant="outlined"
                      label="Peso por la mañana "
                      value={poidsAM}
                      onChange={(e) => setPoidsAM(parseInt(e.target.value))}
                    ></TextField>
                  </Grid>
                  <Grid item xs={3} style={{ textAlign: "center" }}>
                    <TextField
                      variant="outlined"
                      label="Peso por la tarde"
                      value={poidsPM}
                      onChange={(e) => setPoidsPM(parseInt(e.target.value))}
                    ></TextField>
                  </Grid>
                  <Grid item xs={3}>
                    <Button
                      onClick={() => handleConfirm()}
                      variant="contained"
                      color="primary"
                    >
                      Confirmar
                    </Button>
                  </Grid>
                </Grid>
              </CardBody>
            </Card>
          ) : null}
        </Grid>
      </Grid>
    </div>
  );
}
